<template>
  <div class="success">
    <SuccessHead />
    <SuccessStep />
    <img src="@/assets/successRegistration/MiaoHui/logo_MH.png" alt="">
  </div>
</template>

<script>
import SuccessHead from './components/SuccessHead'
import SuccessStep from './components/SuccessStep'
import api from '@/api/apis'
import { MhtestAb } from './abTest'
export default {
  components: { SuccessHead, SuccessStep },
  async beforeRouteEnter (to, from, next) {
    const phone = localStorage.getItem('buyPhone') || ''
    const orderNum = localStorage.getItem('orderNum')
    const productSn = localStorage.getItem('productSn')
    const isPay = localStorage.getItem('is_pay') || ''
    localStorage.removeItem('is_pay')
    api.userOperator.addKefu({ order_num: orderNum, product_sn: productSn, phone, is_pay: isPay })
      .then(res => {
        if (res.group === 'B') {
          next({
            name: 'GetClass',
            query: {
              url: res.qrUrl,
              wxId: res.wechatNum
            }
          })
        } else {
          MhtestAb(next, isPay)
        }
      }).catch(err => {
        console.log(err)
        MhtestAb(next, isPay)
      })
  }
}
</script>

