import api from '@/api/apis'
import log from '@/libs/log'
import { getSearch } from '@/utils'

export async function MhtestAb (next, isSave) {
  const phone = localStorage.getItem('buyPhone') || ''
  const orderNum = localStorage.getItem('orderNum')
  const query = getSearch()
  api.userOperator.getGroup({ domain: 'guide', abKey: 'up_miniprogram_fenban', whiteParam: phone, factor: orderNum, isSave })
    .then(res => {
      if (res.group === 'B') {
        next({
          name: 'MhtestB'
        })
      } else {
        next()
      }
      const exparam = JSON.stringify({
        ab_key: ['Throw', 'guide', 'up_miniprogram_fenban'].join('_'),
        ab_group: res.group,
        ab_uniq_user: res.uniqFlag,
        medId: query.medId
      })
      localStorage.setItem('alternate', exparam)
      log.update({
        alternate: exparam
      })
    }).catch(err => {
      console.log(err)
      next({
        name: 'MhtestB'
      })
    })
}
