<template>
  <div class="success__head">
    <img src="@/assets/successRegistration/MiaoHui/icon_title.png" alt="">
    <h5>报名成功</h5>
    <p>关注<span>【{{ title }}】</span>微信公众号<br />进群学习</p>
    <div class="success__head__copy">
      <img :src="image" alt="">
      <h5>{{ title }}</h5>
      <button @click="actions"><img src="@/assets/successRegistration/MiaoHui/icon_copyBtn.gif" alt=""></button>
    </div>
  </div>
</template>

<script>
import iconOfficial from '@/assets/successRegistration/MiaoHui/icon_official.png'
import { copyText } from '@/utils/index.js'
export default {
  data () {
    return {
      title: '秒会财商课',
      image: iconOfficial
    }
  },
  async created () {
    await this.getGrop()
    this.$log.show({
      module_ori: '基于页面',
      module_part: '106_toufang_lcx_0143'
    })
  },
  methods: {
    async getGrop () {
      const orderNum = localStorage.getItem('orderNum')
      if (!orderNum) return
      const info = await this.$apis.userOperator.getTestGroup({
        domain: 'mh_wechat',
        abKey: 'show',
        userFlag: orderNum,
        isSave: true
      })
      if (!info.group) return
      this.exParam = {
        ab_key: ['Throw', 'mh_wechat', 'show'].join('_'),
        ab_group: info.group,
        ab_uniq_user: orderNum
      }
      this.$log.update({
        alternate: JSON.stringify(this.exParam)
      })
    },
    actions () {
      this.$log.click({
        current_page: location.href,
        module_ori: '一键复制模块',
        module_part: '106_toufang_lcx_0144'
      })
      const orderNum = localStorage.getItem('orderNum')
      copyText('秒会财商课', '复制成功，去微信搜索吧!')
      this.$apis.userOperator.getQuickCopy({
        order_num: orderNum
      })
      if (this.exParam.ab_group === 'B') {
        return this.goofficial()
      }
      if (this.exParam.ab_group === 'C') {
        return this.goMini()
      }
      return this.copy()
    },
    async goofficial () {
      const query = this.getSearch()
      const { url } = await this.$apis.userOperator.getMiniHref({
        path: 'callback/pages/official/index',
        appid: 'wx69a543fde6e86cc7',
        query
      })
      window.location.href = url
    },
    async goMini () {
      const query = this.getSearch()
      const { url } = await this.$apis.userOperator.getMiniHref({
        path: 'callback/pages/loading/loading',
        appid: 'wx69a543fde6e86cc7',
        query
      })
      window.location.href = url
    },
    copy () {
      setTimeout(() => {
        window.location.href = 'weixin://'
      }, 2000)
    },
    getSearch (paramName) {
      const search = window.location.search.substr(1)
      if (!search) return {}
      const query = {}
      search.split('&').forEach(item => {
        const tmp = item.split('=')
        query[tmp[0]] = tmp[1]
      })
      return paramName ? query[paramName] : query
    }

  }
}
</script>

